import React from 'react'
import { FaUser, FaEnvelope, FaPhoneAlt, FaMapMarkerAlt, FaCalendar, FaEdit, FaCamera } from 'react-icons/fa'

const page = () => {
  const user = {
    name: 'John Doe',
    role: 'Church Administrator',
    email: 'john.doe@church.com',
    phone: '+1 (555) 123-4567',
    address: '123 Church Street, City, State 12345',
    joinDate: 'January 15, 2020',
    avatar: null
  }

  return (
    <div className='w-full h-full p-6 bg-gray-50'>
      <div className='max-w-4xl mx-auto'>
        <div className='bg-white rounded-lg shadow-md overflow-hidden'>
          <div className='bg-amber-400 h-32'></div>
          
          <div className='relative px-6 pb-6'>
            <div className='flex flex-col md:flex-row md:items-end md:justify-between'>
              <div className='flex flex-col md:flex-row md:items-end -mt-16 mb-4'>
                <div className='relative'>
                  <div className='w-32 h-32 bg-gray-300 rounded-full border-4 border-white flex items-center justify-center'>
                    {user.avatar ? (
                      <img src={user.avatar} alt={user.name} className='w-full h-full rounded-full object-cover' />
                    ) : (
                      <FaUser className='text-gray-500 text-5xl' />
                    )}
                  </div>
                  <button className='absolute bottom-0 right-0 bg-persian-blue-600 p-2 rounded-full text-white hover:bg-persian-blue-700'>
                    <FaCamera />
                  </button>
                </div>
                
                <div className='md:ml-6 mt-4 md:mt-0 md:mb-2'>
                  <h1 className='text-3xl font-bold text-gray-800'>{user.name}</h1>
                  <p className='text-gray-600'>{user.role}</p>
                </div>
              </div>
              
              <button  
              // onClick={}
               className='bg-persian-blue-600 text-white px-6 py-2 rounded-lg hover:bg-persian-blue-700 flex items-center gap-2 self-start md:self-auto md:mb-2'>
                <FaEdit /> Edit Profile
              </button>
            </div>

            <div className='grid grid-cols-1 md:grid-cols-2 gap-6 mt-8'>
              <div className='flex items-center gap-4 p-4 bg-gray-50 rounded-lg'>
                <FaEnvelope className='text-persian-blue-600 text-xl' />
                <div>
                  <p className='text-sm text-gray-500'>Email</p>
                  <p className='text-gray-800 font-medium'>{user.email}</p>
                </div>
              </div>

              <div className='flex items-center gap-4 p-4 bg-gray-50 rounded-lg'>
                <FaPhoneAlt className='text-persian-blue-600 text-xl' />
                <div>
                  <p className='text-sm text-gray-500'>Phone</p>
                  <p className='text-gray-800 font-medium'>{user.phone}</p>
                </div>
              </div>

              <div className='flex items-center gap-4 p-4 bg-gray-50 rounded-lg'>
                <FaMapMarkerAlt className='text-persian-blue-600 text-xl' />
                <div>
                  <p className='text-sm text-gray-500'>Address</p>
                  <p className='text-gray-800 font-medium'>{user.address}</p>
                </div>
              </div>

              <div className='flex items-center gap-4 p-4 bg-gray-50 rounded-lg'>
                <FaCalendar className='text-persian-blue-600 text-xl' />
                <div>
                  <p className='text-sm text-gray-500'>Member Since</p>
                  <p className='text-gray-800 font-medium'>{user.joinDate}</p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className='grid grid-cols-1 md:grid-cols-2 gap-6 mt-6'>
          <div className='bg-white rounded-lg shadow-md p-6'>
            <h2 className='text-xl font-bold text-gray-800 mb-4'>Activity Summary</h2>
            <div className='space-y-3'>
              <div className='flex justify-between'>
                <span className='text-gray-600'>Events Attended</span>
                <span className='font-bold text-gray-800'>45</span>
              </div>
              <div className='flex justify-between'>
                <span className='text-gray-600'>Groups Joined</span>
                <span className='font-bold text-gray-800'>3</span>
              </div>
              <div className='flex justify-between'>
                <span className='text-gray-600'>Volunteer Hours</span>
                <span className='font-bold text-gray-800'>120</span>
              </div>
            </div>
          </div>

          <div className='bg-white rounded-lg shadow-md p-6'>
            <h2 className='text-xl font-bold text-gray-800 mb-4'>Contributions</h2>
            <div className='space-y-3'>
              <div className='flex justify-between'>
                <span className='text-gray-600'>Total Donations</span>
                <span className='font-bold text-gray-800'>$2,450</span>
              </div>
              <div className='flex justify-between'>
                <span className='text-gray-600'>This Year</span>
                <span className='font-bold text-gray-800'>$850</span>
              </div>
              <div className='flex justify-between'>
                <span className='text-gray-600'>Last Donation</span>
                <span className='font-bold text-gray-800'>Dec 15, 2024</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default page
